import axios from 'axios'
import qs from 'qs'
import { Toast } from 'antd-mobile'
import { getToken } from '../utils/auth'

import store from '../redux/store/index'
import { setBrowserMask } from '../redux/actions'

// import createHistory from 'history/createHashHistory'
// const history = createHistory()
const baseURL = process.env.NODE_ENV === 'development' ? '' : 'https://test-huodong-api.ksgame.com'

// create an axios instance
const service = axios.create({

  baseURL, // apibase_url
  headers: {
    // `headers` 
    'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',
    'X-Requested-With': 'XMLHttpRequest',
    'LOGIN-TYPE': 'wechat'
  },

  // `transformRequest` 
  //  'PUT', 'POST'  'PATCH' 
  //  ArrayBuffer Stream
  transformRequest: [
    function(data) {
      //  data 
      return qs.stringify(data)
    }
  ]
})

// request interceptor
service.interceptors.request.use(config => {
  // token
  if (getToken()) {
    config.headers['TOKEN'] = getToken()
  }

  // get304200
  if (config.method === 'get') {
    if (config.params) {
      config.params['_'] = +new Date()
    } else {
      config.params = { '_': +new Date() }
    }
  }
  return config
}, error => {
  // Do something with request error
  console.log(error) // for debug
  return Promise.reject(error)
})

// respone interceptor
service.interceptors.response.use(
  response => {
    const res = response.data
    if (res.code !== 10000) {
      // 
      if (res.code === 10003) {
        const { redirect_url } = res.data
        window.location.href = redirect_url
      } else if (res.code === 10005) {
      // 
        store.dispatch(setBrowserMask(true))
      } else {
        Toast.info(res.msg)
      }
      return Promise.reject(res)
    } else {
      return response.data
    }
  },
  error => {
    console.log('err' + error)// for debug
    let msg = error.message
    // 
    if (error.code === 'ECONNABORTED' && msg.indexOf('timeout') !== -1) {
      msg = ''
    }
    Toast.fail(msg)

    return Promise.reject(error)
  })

export default service



