import React from 'react'
import LoadingMask from '../LoadingMask/index'

const LoadingComponent = ({ isLoading, error }) => {
  if (isLoading) {
    return LoadingMask()
  }
  else if (error) {
    return <div className="flex flex-center"></div>
  }
  else {
    return null
  }
}

export default LoadingComponent


